import React from 'react';
import ReactDOM from 'react-dom';

import './index.css';

const drumData = [
  {
    id: "hiHat",
    keyPressed: "Q",
    audioClip: "https://s3.amazonaws.com/freecodecamp/drums/Dsc_Oh.mp3"
  },
  {
    id: "bassKick",
    keyPressed: "W",
    audioClip: "https://s3.amazonaws.com/freecodecamp/drums/Kick_n_Hat.mp3"
  },
  {
    id: "hiHatStick",
    keyPressed: "E",
    audioClip: "https://s3.amazonaws.com/freecodecamp/drums/Bld_H1.mp3"
  },
  {
    id: "punchyKick",
    keyPressed: "A",
    audioClip: "https://s3.amazonaws.com/freecodecamp/drums/punchy_kick_1.mp3"
  },
  {
    id: "sideStick",
    keyPressed: "S",
    audioClip: "https://s3.amazonaws.com/freecodecamp/drums/side_stick_1.mp3"
  },
  {
    id: "snareStick",
    keyPressed: "D",
    audioClip: "https://s3.amazonaws.com/freecodecamp/drums/Brk_Snr.mp3"
  },
  {
    id: "cev",
    keyPressed: "Z",
    audioClip: "https://s3.amazonaws.com/freecodecamp/drums/Cev_H2.mp3"
  },
  {
    id: "giveUsALight",
    keyPressed: "X",
    audioClip: "https://s3.amazonaws.com/freecodecamp/drums/Give_us_a_light.mp3"
  },
  {
    id: "closedHiHat",
    keyPressed: "C",
    audioClip: "https://s3.amazonaws.com/freecodecamp/drums/Dry_Ohh.mp3"
  }
];

class DrumPad extends React.Component {
  render() {
    return (
      <div>
        <button className="drum-pad" id={this.props.drum.id} >
          <audio className="clip" id={this.props.drum.keyPressed} onKeyDown={this.props.handleKeyPress}>
            <source src={this.props.drum.audioClip} type="audio/mpeg"/>
            <p>Your browser doesn't support HTML5 audio.</p>
          </audio>
          {this.props.drum.keyPressed}
        </button>
      </div>
    );
  }
}

class App extends React.Component {
  constructor(props) {
    super(props);
    this.handleKeyPress = this.handleKeyPress.bind(this);
  }
  componentDidMount() {
    document.addEventListener("keydown", this.handleKeyPress)
  }
  componentWillUnmount() {
    document.removeEventListener("keydown", this.handleKeyPress)
  }
  handleKeyPress(event) {
    const availKeys = ["Q", "W", "E", "A", "S", "D", "Z", "X", "C"];
    const availKeysCodes = availKeys.map(key => key.charCodeAt());

    if(availKeysCodes.includes(event.keyCode)) {
      let keyPressed = document.getElementById(event.key.toUpperCase());
      keyPressed.play();
    }
  }

  render() {
    const drumSet = drumData.map(item => <DrumPad key={item.id} drum={item} />);

    return (
      <div id="drum-machine">
        <div className="grid">
          {drumSet}
        </div>
        <p id="display">Placeholder text</p>
      </div>
    );
  }
}

ReactDOM.render(<App />, document.getElementById("root"));
